.container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 95vh;
    font-size: var(--p-font-size);
    line-height: 1.4;
  }

.card__container{
    width: 415px;   
    background: linear-gradient(217deg, var(--medium-dark), var(--dark-blue));
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    align-content: flex-start;
    flex-direction: column;
    padding: 2rem;
    border-radius: 2rem;
    gap: 1.5rem;
}

.card__container h1{
    color: var(--white-color);
}

.card__container p{
    color: var(--light-grey);
    font-size: 0.9rem;
}

.card__container img{
    background: var(--medium-dark);
    padding: 1rem;
    border-radius: 50px;
}

.card__container_ratings{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    align-content: center;
    flex-direction: row;
}

.card__container_ratings_number {
    background: var(--medium-dark);
    width: 3rem;
    height: 3rem;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    color: var(--light-grey);
    font-size: 0.8rem;
}

.card__container_ratings_number:hover{
    background: var(--orange-color);
    color: var(--white-color);
    cursor: pointer;
}

.card__container_ratings_number_isactive{
    background: var(--light-grey);
    width: 3rem;
    height: 3rem;
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    font-size: 0.8rem;
    color: var(--white-color);
}

.card__container_ratings_number_isactive:hover{
    background: var(--orange-color);
    color: var(--white-color);
    cursor: pointer;
}

.card__container a{
    width: 100%;
    padding: 1rem 0;
    background: var(--orange-color);
    color: var(--white-color);
    font-weight: 700;
    text-transform: uppercase;
    border: none;
    border-radius: 2.5rem;
    letter-spacing: 0.2rem;
    text-align: center;
    text-decoration: none;
    font-size: 0.9rem;
}

.card__container a:hover{
    background: var(--white-color);
    color: var(--orange-color);
}

@media screen and (max-width: 500px) {
    .container{
        padding: 0 1.5rem;
    }
    .card__container{
        width: 100%;
      }

  }